.sidebarContainer{
width: 28%;
}

.cardHeader{
    text-transform: capitalize;
    font-size: 15px;
    text-align: center;
    padding: 10px 0;
    box-sizing: border-box;
    letter-spacing: 2px;
    color: #333;
}
.profileimageContainer{
    width: 100%;
    box-sizing: border-box;
}

.profileimageContainer img{
    max-width: 100%;
    max-height:100% ;
    margin: 10px 0;
}
.personalInfo{
    font-size: 16px;
    color: #565673;
    font-weight: 300;
}

.recentPosts{
    padding: 20px 0;
}
.recentPost{
    border-bottom: 1px solid#6c6c6c;

}

.recentPost h3{
    color: #333;
    letter-spacing: 1px;
    font-size: 15px;
    font-weight: 500;
    margin: 0;
    text-decoration:none;
    text-align: center;
    
}
.recentPost span{
    font-size: 15px;
    font-weight: 400;
    color: #6c6c6c;
    text-align: center;

}

.mar {
    margin: 0 10px !important;

}

